<template>
  <div class="login">
  		<mt-header fixed title="登录">
		    <mt-button icon="back" slot="left" @click="goback"></mt-button>
  		</mt-header>
			<!-- tab-container -->
			<div style="margin-top:40px;">
				<form>
					<mt-field label="用户名" placeholder="请输入用户名" type="text" v-model="username"></mt-field>
					<mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
					<mt-cell title="自动登录">
						<mt-switch v-model="toggleFlag"></mt-switch>
					</mt-cell>
					<div class="submit-box mint-field">
  					<router-link to="/">
							<mt-button size="large" type="primary">登录</mt-button>
			  		</router-link>
  					<router-link to="/register">
							<mt-button size="large" type="danger">没账号？立即注册</mt-button>
			  		</router-link>
					</div>
				</form>
			</div>
  </div>
</template>

<script>
export default {
  name: 'login',
  title: 'login',
  data () {
    return {
    	msg: 'hello,vue',
    	username: '',
    	password: '',
    	toggleFlag: false,
  	}
  },
  methods: {
  	goback(){
			this.$router.goBack();
		}
  }
}
</script>
<style>
	.submit-box {
		margin: 0.625rem;
	}
	.submit-box > a {
		text-decoration: none;
    width: 100%;
	}
	.submit-box > a:first-child {
		margin-right: 0.625rem;
	}
</style>


