<template>
  <div class="register">
  		<mt-header fixed title="注册">
		    <mt-button icon="back" slot="left" @click="goback"></mt-button>
  		</mt-header>
			<!-- tab-container -->
			<div style="margin-top:40px;">
				<form>
					<mt-field placeholder="请输入用户名" v-model="username"></mt-field>
					<mt-field placeholder="请输入密码" type="password" v-model="password"></mt-field>
					<mt-field placeholder="请再次输入密码" type="password" v-model="psw"></mt-field>
					<mt-field placeholder="请输入邮箱" type="email" v-model="email"></mt-field>
					<mt-field placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
					<div class="submit-box mint-field">
  					<router-link to="/">
							<mt-button size="large" type="danger">注册</mt-button>
			  		</router-link>
  					<router-link to="/login">
							<mt-button size="large" type="primary">已有账号？去登录</mt-button>
			  		</router-link>
					</div>
				</form>
			</div>
  </div>
</template>

<script>
export default {
  name: 'register',
  title: 'register',
  data () {
    return {
    	msg: 'hello,vue',
    	username: '',
    	password: '',
    	psw: '',
    	email: '',
    	phone: '',
  	}
  },
  methods: {
  	goback(){
			this.$router.goBack();
		}
  }
}
</script>
<style>
	.submit-box {
		margin: 0.625rem;
	}
	.submit-box > a {
		text-decoration: none;
    width: 100%;
	}
	.submit-box > a:first-child {
		margin-right: 0.625rem;
	}
</style>


