<template>
	<div class="refresh">
		<mt-header fixed title="加载刷新">
		    <mt-button icon="back" slot="left" @click="goback"></mt-button>
		</mt-header>
		<!-- tab-container -->
		<div class="mt-refresh-scroll">
			<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" :max-distance="150"
                @top-status-change="handleTopChange" @bottom-status-change="handleBottomChange" ref="loadmore">
		        <p slot="top" class="mint-loadmore-top">
			        <span v-show="topStatus === 'pull'" :class="{ 'rotate': topStatus === 'drop' }">
			        	<icon name="caret-up"></icon>
			        </span>
			        <span v-show="topStatus === 'loading'">
			        	<mt-spinner type="fading-circle" color="dodgerblue" :size="20"></mt-spinner>
			        	<!--<icon class="animate-rotate" name="refresh"></icon>-->
			        </span>
			        <span v-show="topStatus === 'drop'">释放刷新</span>
		        </p>
		        <p slot="bottom" class="mint-loadmore-bottom">
			        <span v-show="bottomStatus === 'pull'" :class="{ 'rotate': bottomStatus === 'drop' }">
				        	<icon name="angle-double-down"></icon>
			        </span>
			        <span v-show="bottomStatus === 'loading'">
			        	<mt-spinner type="triple-bounce" color="dodgerblue" :size="20"></mt-spinner>
			        </span>
			        <span v-show="bottomStatus === 'drop'">释放加载</span>
		        </p>
		        <mt-cell v-for="(item,index) in list" :key="index">
		        	<span slot="title" @click="itemClick(item)">{{ item }}</span>
		        </mt-cell>
    		</mt-loadmore>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'refresh',
		title: 'refresh',
		data() {
			return {
				msg: 'hello,vue',
				pageNo:1,
  				pageSize:3,
				list: [],
				allLoaded: false, //是否可以上拉属性，false可以上拉，true为禁止上拉，就是不让往上划加载数据了  
				topStatus: '',
				bottomStatus: '',
			}
		},
		created: function(){
			var i = 0,
				len = 10;
			for(; i < len; i++){
				this.list.push('demo' + i);
			}
		},
		methods: {
			loadTop: function(){ // 刷新数据的操作
				var self = this;
				setTimeout(function(){
					self.list.splice(0, self.list.length);
					var i = 0,
					len = 10;
					for(; i < len; i++) {
						self.list.push('demo' + i);
					}
					self.$refs.loadmore.onTopLoaded();
				}, 2000);
				self.pageNo = 1;
				this.allLoaded = false;
			},
			loadBottom: function() { // 加载更多数据的操作
				//load data
				//this.allLoaded = true;// 若数据已全部获取完毕
				var self = this;
				setTimeout(function() {
					var i = 0,
					len = 10;
					for(; i < len; i++) {
						self.list.push('dddd' + i);
					}
					self.$refs.loadmore.onBottomLoaded();
				}, 2000);
				self.pageNo ++;
				this.$nextTick(function (){
			        // 是否还有下一页，加个方法判断，没有下一页要禁止上拉
			        this.isHaveMore(self.pageNo);
		        });
			},
			handleTopChange: function(status){
				this.topStatus = status;
			},
			handleBottomChange: function(status){
				this.bottomStatus = status;
			},
			itemClick: function(data){
				console.log('item click, msg : ' + data);
			},
			isHaveMore: function(page){
				if(page == this.pageSize){
					this.allLoaded = true;
				}
			},
			goback(){
				this.$router.goBack();
			}
		}
	}
</script>
<style>
	.mint-spinner-fading-circle {
		margin: 0 auto;
    	top: 15px;
	}
	.animate-rotate {
		animation: run-inner 1s infinite linear;
	}
	@keyframes run-inner {
		0% {
			transform: rotate(0deg)
		}
		25% {
			transform: rotate(-90deg)
		}
		50% {
			transform: rotate(-180deg)
		}
		75% {
			transform: rotate(-270deg)
		}
		100% {
			transform: rotate(-360deg)
		}
	}
</style>


