<template>
  <div class="demo">
  		<mt-header fixed title="demo">
		    <mt-button icon="back" slot="left" @click="goback"></mt-button>
			 	<router-link to="/lazy" slot="right">
			  	<mt-button icon="more"></mt-button>
			  </router-link>
  		</mt-header>
			<!-- tab-container -->
			<div class="mt-scroll">
				<mt-navbar v-model="selected">
				  <mt-tab-item id="1">Css</mt-tab-item>
				  <mt-tab-item id="2">Js</mt-tab-item>
				  <mt-tab-item id="3">Form</mt-tab-item>
				</mt-navbar>
				<mt-tab-container v-model="selected">
				  <mt-tab-container-item id="1">
				  	<mt-cell title="标题文字" is-link to="/#">
						  <span style="color: dodgerblue">这里是元素</span>
						</mt-cell>
						<mt-cell title="标题文字" value="带 icon">
							<icon slot="icon" name="arrows" width="30" height="30" color="#6cf" style="vertical-align:middle"></icon>
						</mt-cell>
				  	<mt-cell title="标题文字">
						  <span>icon 是图片</span>
						  <img slot="icon" src="../assets/logo.png" width="24" height="24">
						</mt-cell>
				  	<mt-cell title="标题文字" is-link>
						  <span style="color: green">这里是元素</span>
						</mt-cell>
				  	<mt-cell title="标题" label="描述信息" is-link></mt-cell>
				  	<mt-cell title="标题文字" is-link>
				  		<mt-button icon="back" size="small" type="primary">按钮</mt-button>
						</mt-cell>
				  	<mt-cell-swipe
					  title="标题文字swipe"
					  :right="[
					    {
					      content: 'Delete',
					      style: { background: 'red', color: '#fff' },
					      handler: () => this.$messagebox('delete')
					    }
					  ]"></mt-cell-swipe>
					  <mt-cell title="snake" isLink>
				  		<mt-spinner :type="0" color="dodgerblue" :size="20"></mt-spinner>
					  </mt-cell>
					  <mt-cell title="double-bounce" isLink>
				  		<mt-spinner :type="1" color="dodgerblue" :size="20"></mt-spinner>
					  </mt-cell>
					  <mt-cell title="triple-bounce" isLink>
				  		<mt-spinner :type="2" color="dodgerblue" :size="20"></mt-spinner>
					  </mt-cell>
					  <mt-cell title="fading-circle" isLink>
				  		<mt-spinner :type="3" color="dodgerblue" :size="20"></mt-spinner>
					  </mt-cell>
				  </mt-tab-container-item>
				  <mt-tab-container-item id="2">
				  	
				  	<mt-datetime-picker
				  		ref="picker2"
						  v-model="pickerVisible"
						  type="date"
						  :startDate="startDate"
						  :endDate="endDate"
						  year-format="{value} 年"
						  month-format="{value} 月"
						  date-format="{value} 日"
						  @confirm="handleConfirm">
						</mt-datetime-picker>
						
				  	<mt-datetime-picker
						    ref="picker1"
						    type="time"
						    v-model="pickerValue">
						</mt-datetime-picker>
				  	<mt-popup
						  v-model="popupVisible"
						   position="left" style="right: 0;">
							  <!--<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
							    <mt-cell v-for="n in 13"  title="tab-container 1"></mt-cell>
								</mt-loadmore>-->
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
								<mt-cell title="popup">
			  					<mt-button type="primary" size="small">popupButton</mt-button>
								</mt-cell>
						</mt-popup>
				  	<mt-actionsheet :actions="actions"　v-model="sheetVisible"></mt-actionsheet>
				  	
				  	
				  	
				  	<mt-cell>
				  		<span slot="title">{{selectDate}}</span>
				  		<mt-button type="primary" size="small" @click.native ="handleClick12">选择日期</mt-button>
				  	</mt-cell>
				  	<mt-cell>
				  		<span slot="title">
				  			<span class="mint-cell-text">datetime-picker</span>
				  			<span class="mint-cell-label">{{pickerValue}}</span>
				  		</span>
				  		<mt-button type="primary" size="small" @click.native ="handleClick11">选择时间</mt-button>
				  	</mt-cell>
				  	<mt-cell class="page-progress">
				  		<span slot="title">
				  			<span class="mint-cell-text">progress</span>
				  			<span class="mint-cell-label">{{progressValue}}%</span>
				  		</span>
				  	  <mt-progress v-model="progressValue" :bar-height="5"></mt-progress>
				  		<mt-button type="primary" size="small" @click.native ="handleClick10">启动</mt-button>
				  	</mt-cell>
				  	<mt-cell class="page-range">
				  		<span slot="title">
				  			<span class="mint-cell-text">range</span>
				  			<span class="mint-cell-label">{{rangeValue}}</span>
				  		</span>
				  		<mt-range v-model="rangeValue"
				  			:min="10"
							  :max="90"
							  :step="2"
							  :bar-height="5">
				  		</mt-range>
				  	</mt-cell>
				  	<mt-cell title="popup">
				  		<mt-button type="primary" size="small" @click.native ="handleClick9">popup</mt-button>
				  	</mt-cell>
				  	<mt-cell title="actionsheet">
				  		<mt-button type="primary" size="small" @click.native ="handleClick8">actionsheet</mt-button>
				  	</mt-cell>
				  	<mt-cell title="MessageBox">
				  		<mt-button type="primary" size="small" @click.native ="handleClick5">alert</mt-button>
				  	</mt-cell>
				  	<mt-cell title="MessageBox">
				  		<mt-button type="primary" size="small" @click.native ="handleClick6">confirm</mt-button>
				  	</mt-cell>
				  	<mt-cell title="MessageBox">
				  		<mt-button type="primary" size="small" @click.native ="handleClick7">prompt</mt-button>
				  	</mt-cell>
				  	<mt-cell title="toast">
				  		<mt-button type="primary" size="small" @click.native="handleClick1">toast</mt-button>
				  	</mt-cell>
				  	<mt-cell title="icon-toast">
				  		<mt-button type="primary" size="small" @click.native="handleClick2">toast</mt-button>
				  	</mt-cell>
				  	<mt-cell title="Indicator">
				  		<mt-button type="primary" size="small" @click.native="handleClick3">Indicator</mt-button>
				  	</mt-cell>
				  	<mt-cell title="spinner">
				  		<mt-button type="primary" size="small" @click.native="handleClick4">spinner</mt-button>
				  	</mt-cell>
				  </mt-tab-container-item>
				  <mt-tab-container-item id="3">
						<mt-cell title="badge" isLink>
							<mt-badge type="primary">30</mt-badge>
						</mt-cell>
				  	<mt-field label="用户名" placeholder="请输入用户名"></mt-field>
						<mt-field label="邮箱" placeholder="请输入邮箱" type="email" ></mt-field>
						<mt-field label="密码" placeholder="请输入密码" type="password" ></mt-field>
						<mt-field label="手机号" placeholder="请输入手机号" type="tel"></mt-field>
						<mt-field label="网站" placeholder="请输入网址" type="url"></mt-field>
						<mt-field label="数字" placeholder="请输入数字" type="number" ></mt-field>
						<mt-field label="生日" placeholder="请输入生日" type="date" ></mt-field>
						<mt-field label="自我介绍" placeholder="自我介绍" type="textarea" rows="4"></mt-field>
				  	
				  	
				  	
				  	<mt-cell>
				  		<span slot="title">{{value4}}</span>
				  		<mt-switch v-model="value4"></mt-switch>
					  </mt-cell>
				  	<mt-checklist
						  title="复选框列表"
						  v-model="value1"
						  :options="['选项A', '选项B', '选项C']">
						</mt-checklist>
						<mt-radio
						  title="单选框列表"
						  v-model="value2"
						  :options="['选项A', '选项B', '选项C']">
						</mt-radio>
				  	<mt-checklist
						  v-model="value3"
						  align="right"
						  :options="options">
						</mt-checklist>
				  </mt-tab-container-item>
				</mt-tab-container>
			</div>
			<mt-tabbar fixed v-model="selected">
			  <mt-tab-item id="1">
			    <img slot="icon" src="../assets/logo.png">
			    Css
			  </mt-tab-item>
			  <mt-tab-item id="2">
			    <img slot="icon" src="../assets/logo.png">
			    Js
			  </mt-tab-item>
			  <mt-tab-item id="3" icon="more">
			    <img slot="icon" src="../assets/logo.png">
			    Form
			  </mt-tab-item>
			</mt-tabbar>
  </div>
</template>

<script>
export default {
  name: 'demo',
  title: 'demo',
  data () {
    return {
    	msg: 'hello',
    	selected: '1',
    	value: "",
    	value1: ['选项A'],
    	value2: '0',	
    	value3: ['值A'],
    	value4: true,
    	options:[
			  {
			    label: '被禁用',
			    value: '值F',
			    disabled: true
			  },
			  {
			    label: '选中禁用',
			    value: '选中禁用的值',
			    disabled: true
			  },
			  {
			    label: '选项A',
			    value: '值A'
			  },
			  {
			    label: '选项B',
			    value: '值B'
			  }
			],
			allLoaded: false,
			sheetVisible: false,
			actions:　[{
				name: '拍照',
				method: function(){
					console.log(1)
				}
			},{
				name: '从相册中选择',
				method: function(){
					console.log(2)
				}
			}],
			popupVisible: false,
			rangeValue: 50,
			progressValue: 0,
			timer: '',
			pickerValue: "00:00",
			pickerVisible: new Date('2017-07-25'),
			startDate: new Date('2014-10-25'),
			endDate: new Date(),
			selectDate: 'yy-MM-dd'
  	}
  },
  methods: {
		handleClick1: function() {
  		this.$toast('Hello world!')
    },
    handleClick2: function() {
  		this.$toast({
  			 	message: '提示',
  			 	iconClass: 'mintui mintui-success'
  		})
    },
    handleClick3: function(){
    	var indic =  this.$indicator
    	indic.open();
    	setTimeout(function(){
    		indic.close();
    	},3000)
    },
    handleClick4: function(){
    	var indic =  this.$indicator
    	indic.open({
			  text: '加载中...',
			  spinnerType: 'triple-bounce',
			})
    	setTimeout(()=>{
    		indic.close();
    	},3000)
    },
    handleClick5: function(){
    	this.$messagebox('提示','操作成功');
    },
    handleClick6: function(){
    	this.$messagebox({
			  title: '提示',
			  message: '确定执行此操作?',
			  showCancelButton: true,
			  callback: function(action){
			  	console.log(action)
			  }
			});
//			this.$messagebox.confirm('确定执行此操作?').then(action => {
//				console.log(action)
//			});
    },
    handleClick7: function(){
    	this.$messagebox.prompt('请输入姓名').then(({ value, action }) => {
    		console.log(action)
    		this.$messagebox('您输入的名字是',value);
			})
    },
    handleClick8: function(){
    	this.sheetVisible = true
    },
    handleClick9: function(){
    	this.popupVisible = true
    },
    handleClick10: function(){
    	if(this.progressValue >= 0 && this.progressValue < 100){
	    	var timer = this.timer;
	    	if(timer){
	    			clearInterval(timer);
				}
	    	timer = setInterval(()=>{
	    		this.progressValue += 1;
	    		if(this.progressValue == 100){
	    			clearInterval(timer);
	    		}
	    	},20)
	    }
    },
    handleClick11: function(){
    	this.$refs.picker1.open();
    },
    handleClick12: function(){
    	this.$refs.picker2.open();
    },
    handleConfirm: function(date){
    	var y = date.getFullYear();
    	var m = date.getMonth() + 1;
    	m = m < 10 ? '0' + m : m;  
	    var d = date.getDate();  
	    d = d < 10 ? '0' + d : d; 
    	this.selectDate = y + "-" + m  + "-" + d;
    	this.$toast(this.selectDate);
    },
    loadTop: function() {
		  // 加载更多数据
		  console.log("top");
		  this.$refs.loadmore.onTopLoaded();
		},
    loadBottom: function() {
//		  console.log("bottom");
//		  this.allLoaded = true;// 若数据已全部获取完毕
//		  this.$refs.loadmore.onBottomLoaded();
		},
		goback(){
			this.$router.goBack();
		}
  },
  mounted: function(){
  	var date = this.pickerVisible;
  	var y = date.getFullYear();
		var m = date.getMonth() + 1;
		m = m < 10 ? '0' + m : m;  
    var d = date.getDate();  
    d = d < 10 ? '0' + d : d; 
		this.selectDate = y + "-" + m  + "-" + d;
  }
}
</script>
<style>
	.mt-scroll {
		overflow-y: auto;
		position: absolute;
		top: 40px;
		bottom: 55px;
		width: 100%;
	}
	.page-range .mint-cell-value,.page-progress .mint-cell-value {
    -webkit-box-flex: 2.5;
    -ms-flex: 2.5;
    flex: 2.5;
    position: relative;
	}
	.page-progress .mint-cell-value {
		justify-content: space-between;
	}
	.page-range .mt-range ,.page-progress .mt-progress {
		width: 100%;
	}
	.page-progress .mt-progress {
		width: 75%;
	}
</style>


