<template>
  <div id="app">
   		<transition class="" :name="transitionName"> 
				<router-view class="Router"></router-view>
			</transition>
  </div>
</template>

<script>
export default {
  name: 'App',
	data() {　　　　
		return {　　　　　　
			transitionName: 'slide-right' // 默认动态路由变化为slide-right
		}　　
	},
	watch: {　　　
		'$route' (to, from) {　　　　
			let isBack = this.$router.isBack //  监听路由变化时的状态为前进还是后退
			if(isBack) {　　　　　　　　
				this.transitionName = 'slide-right'　　　　　　
			} else {　　　　　　
				this.transitionName = 'slide-left'　　　　　
			}　　
			this.$router.isBack = false　　
		}　
	}
}
</script>
<style>

.Router {
	transition: all .5s ease-in;
}

.slide-left-enter,.slide-right-leave-active {
	opacity: 0;
	-webkit-transform: translate(100%, 0);
	transform: translate(100%, 0);
}

.slide-left-leave-active,
.slide-right-enter {
	 opacity: 0;
	-webkit-transform: translate(-100%, 0);
	transform: translate(-100% 0);
}
</style>
<style>
	.mt-scroll {
		overflow-y: auto;
		position: absolute;
		top: 40px;
		bottom: 55px;
		width: 100%;
	}
	.mt-refresh-scroll {
		margin-top: 40px;
		height: calc(100vh - 40px);
		overflow-x: auto;
	}
	.mint-cell-wrapper {
		background-image: -webkit-linear-gradient(left, #eee, #efefef 80%, transparent 100%);
	}
	.mint-cell:last-child {
		background-image: -webkit-linear-gradient(left, #eee, #efefef 100%, transparent 100%);
	}
	.mint-cell-wrapper {
		font-size: 0.875rem;
	}
	.back-btn {
		position: absolute;
		bottom: 30px;
    left: 50%;
    width: 74px;
    margin-left: -37px;
	}
	.back-btn .mint-button-icon {
		float: right;
	}
	.mint-button {
		font-size: 1rem;
	}
	.mint-cell-title .fa-icon {
		vertical-align: middle;
	}
	.fa-icon {
		color: #26a2ff;
	}
	.mint-header-button .fa-icon {
		color: #fff;
	}
	.mint-button--primary .fa-icon  {
		color: #fff;
	}
</style>


