<template>
  <div class="search">
  	<form action="">
	  	<mt-search v-model="value">
			  <mt-cell
			    v-for="(item,index) in result"
			    :title="item.title"
			    :value="item.value"
			    :key="index">
			  </mt-cell>
			</mt-search>
		</form>
		<mt-button class="back-btn" type="primary" size="small" @click="goback">
			back
			<icon slot="icon" name="angle-double-down"></icon>
		</mt-button>
  </div>
</template>

<script>
export default {
  name: 'search',
  title: 'search',
  data () {
    return {
    	msg: 'hello,vue',
    	value: 'mint',
    	result: [
    		{
    			title: 'mint搜索标题1',
    			value: '搜索内容1'
    		},
    		{
    			title: 'mint搜索标题2',
    			value: '搜索内容2'
    		},
    		{
    			title: 'mint搜索标题3',
    			value: '搜索内容3'
    		},
    	]
  	}
  },
  methods: {
  	goback(){
			this.$router.goBack();
		}
  },
}
</script>
<style>
	.mint-searchbar {
	  background-color: #26a2ff;
	}
	.mint-searchbar-cancel {
		color: #fff;
		font-size: 0.875rem;
	}
	.mint-searchbar-inner .mintui-search {
    font-size: 18px;
    margin-right: 5px;
	}
	.mint-search-list {
		padding-top: 52px;
	}
	.mint-search-list .mint-cell-wrapper {
		font-size: 0.875rem;
	}
</style>


