import Vue from 'vue'
import Router from 'vue-router'
import home from '@/components/home'
import lazy from '@/components/lazy'
import demo from '@/components/demo'
import search from '@/components/search'
import login from '@/components/login'
import register from '@/components/register'
import refresh from '@/components/refresh'

Router.prototype.goBack = function () { 
　　this.isBack = true
　　window.history.go(-1);
}

Vue.use(Router)
export default new Router({
  routes: [
   	{
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/home',
      name: 'home',
      component: home
    },
    {
      path: '/lazy',
      name: 'lazy',
      component: lazy
    },
    {
      path: '/demo',
      name: 'demo',
      component: demo
    },
    {
      path: '/search',
      name: 'search',
      component: search
    },
    {
      path: '/login',
      name: 'login',
      component: login
    },
    {
      path: '/register',
      name: 'register',
      component: register
    },
     {
      path: '/refresh',
      name: 'refresh',
      component: refresh
    }
  ]
})



