<template>
  <div class="lazy">
  		<mt-header fixed title="懒加载">
  			<!--<router-link to="/" slot="left">
			    <mt-button icon="back"></mt-button>
			  </router-link>-->
		    <mt-button icon="back" slot="left" @click="goback"></mt-button>
  		</mt-header>
			<!-- tab-container -->
			<div style="margin-top:40px;">
				<mt-cell title="lazyload"></mt-cell>
			  <ul class="page-lazyload-list">
				  <li class="page-lazyload-listitem" v-for="item in list">
				    <img class="page-lazyload-image" v-lazy="item">
				  </li>
				</ul>
			</div>
  </div>
</template>

<script>
export default {
  name: 'lazy',
  title: 'lazy',
  data () {
    return {
    	msg: 'hello,vue',
    	list: [
    		'static/images/banner.png',
	    	'static/images/banner.png',
	    	'static/images/banner.png',
	    	'static/images/banner.png'
    	]
  	}
  },
  methods: {
//	返回上一页
		goback(){
			this.$router.goBack();
		}
  }
}
</script>
<style>	
	.page-lazyload-list {
		text-align: center;
	}
	.page-lazyload-listitem {
    height: 13.25rem;
    margin: 0 auto;
    margin-bottom: 10px;
    background-color: #ddd;
	}
	.page-lazyload-image {
    display: block;
    width: 100%;
    height: 100%;
	}
	img[lazy=loading] {
	  width: 40px;
	  height: 13.25rem;
	  margin: auto;
	}
</style>


